body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Position and sizing of burger button */
.bm-burger-button {
    position: fixed;
    width: 36px;
    height: 30px;
    right: 36px;
    top: 36px;
    background: #fff;
    /* border: 10px solid red; */
  }
  
  /* Color/shape of burger icon bars */
  .bm-burger-bars {
    background: #373a47;
  }
  
  /* Position and sizing of clickable cross button */
  .bm-cross-button {
    height: 32px !important;
    width: 32px !important;
    right: 32px !important;
  }
  
  /* Color/shape of close button cross */
  .bm-cross {
    background: #bdc3c7;
    height: 28px !important;
  }
  
  /* General sidebar styles */
  .bm-menu {
    background: #373a47;
    padding: 2.5em 1.5em 0;
    font-size: 1.15em;
  }
  
  /* Wrapper for item list */
  .bm-item-list {
    height: 90% !important;
  }
  
  /* Individual items */
  .library-list-items {
    display: block;
    color: #b8b7ad;
    text-decoration: none;
    padding: 0.5em;
  }

  .sidebar-header {
    color: #b8b7ad;
    margin-top: 0;
  }

  .search-input {
    margin: 0;
    width: 215px;
    margin-bottom: 28px;
  }

  .search-box {
    color: #b8b7ad;
  }

  .filter-area {
    border: 2px solid #b8b7ad;
    margin-bottom: 22px;
    padding: 5px;
  }

  .library-list-items:hover {
    font-weight: bold;
    text-decoration: underline;
    cursor: pointer;
  }

  /* Added styles */
  .bm-menu-wrap {
      -webkit-transition: all 0.1s ease 0s !important;
      transition: all 0.1s ease 0s !important;
  }

  /* Media Queries */
  @media screen and (max-width: 800px) {

    .bm-menu {
      padding-top: 1.5em;
    }

    .bm-menu-wrap {
      height: 35% !important;
      width: 100% !important;
      bottom: 0;
    }
  }

.map-nav {
    position: absolute;
    top: 0;
    left: 0;
    padding: 6px;
}

/* STYLING OF INFOWINDOW POPUPS */
.mapboxgl-popup-content {
    box-shadow: 5px 5px 5px 0px rgba(0,0,0,0.2);
    padding: 15px 30px 15px 15px;
}

.service-indicator {
    position: relative;
}

.infowindow-icon {
    width: 20px;
    height: 20px;
    position: absolute;
    bottom: 0;
}
*:focus {
    outline: 4px solid pink;
}
